html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 130rem;
  margin: 10rem auto 0;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.boton-rosa-block {
  background-color: #F53756;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-rosa-block:hover {
  background-color: #f41f41;
  cursor: pointer;
}

.boton-rosa {
  background-color: #F53756;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-rosa:hover {
  background-color: #f41f41;
  cursor: pointer;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 50;
  background-color: #FFFFFF;
}

.navegacion {
  display: flex;
  align-items: center;
  color: #FFFFFF;
  margin: 1.5rem 2rem;
  gap: 1.5rem;
  height: 4rem;
}
.navegacion__logo {
  color: #E60023;
  font-size: 2.5rem;
}
.navegacion__acciones {
  display: flex;
  align-items: center;
}
.navegacion__acciones i {
  color: #5f5f5f;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .navegacion__acciones i {
    font-size: 2rem;
  }
}
.navegacion__acciones--flecha {
  color: #5f5f5f;
  font-size: 1.4rem !important;
  color: #000000 !important;
}
.navegacion__iconos {
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navegacion__iconos:hover {
  background-color: #e9e9e9;
  border-radius: 50%;
}
.navegacion__iconos img {
  padding: 0.5rem;
  border-radius: 50%;
}

.enlaces {
  display: none;
}
@media (min-width: 768px) {
  .enlaces {
    display: block;
  }
}
.enlaces__enlace {
  color: #000000;
  padding: 1rem;
  font-size: 1.5 rem;
  font-weight: 500;
  font-family: system-ui;
}
.enlaces__enlace--activo {
  color: #FFFFFF;
  background-color: #000000;
  border-radius: 4rem;
}

.buscador {
  background-color: #e9e9e9;
  display: flex;
  align-items: center;
  padding: 0 0 0 1.5rem;
  border-radius: 5rem;
  flex-grow: 1;
}
.buscador input[type=text] {
  background-color: #e9e9e9;
  border-radius: 5rem;
  padding: 1rem;
  border: none;
}
.buscador input[type=text]:focus {
  border: none;
  outline: none;
}
.buscador__icono {
  font-size: 2rem;
  color: #333333;
}

.grid-container {
  margin: 0 auto;
}

.grid-item {
  width: 230px;
  margin-bottom: 40px;
  position: relative;
}
.grid-item img {
  display: block;
  border-radius: 15px;
}
.grid-item:hover .opciones {
  display: flex;
}

.opciones {
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.3215686275);
  border-radius: 15px;
  padding: 1.5rem;
  cursor: zoom-in;
  top: 0;
  left: 0;
}
.opciones__botones {
  text-align: end;
}

.btn_guardar {
  background-color: #E60023;
  color: #FFFFFF;
  border: none;
  padding: 1rem;
  border-radius: 15rem;
  font-size: 1rem;
  font-weight: 600;
}

.icono {
  background-color: #e9e9e9;
  padding: 0.5rem;
  border-radius: 50%;
  font-size: 1.2rem;
}
.icono:hover {
  background-color: #FFFFFF;
  cursor: pointer;
}