@use "base/variables" as v;
@use "base/mixins" as m;


.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 50;
  background-color: v.$blanco;
}
.navegacion {
  display: flex;
  align-items: center;
  color: v.$blanco;
  margin: 1.5rem 2rem;
  gap: 1.5rem;
  height: 4rem;

  &__logo {
    color: v.$rojo;
    font-size: 2.5rem;
  }




  &__acciones {
    display: flex;
    align-items: center;
    // gap: .rem;
    i {
      color: #5f5f5f;
      font-size: 1.4rem;
      @include m.tablet {
        font-size: 2rem;
      }
    }
    &--flecha {
      color: #5f5f5f;
      font-size: 1.4rem !important;
      color: v.$negro !important;
    }
  }

  &__iconos {
    width: 4rem;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      background-color: v.$gris-claro;
      border-radius: 50%;
    }
    img {
      padding: .5rem;
      border-radius: 50%;
    } 
  }
}

.enlaces {
  display: none;

  @include m.tablet {
    display: block;
  }

  &__enlace {
    color: v.$negro;
    padding: 1rem;
    font-size: 1.5    rem;
    font-weight: 500;
    font-family: system-ui;

    &--activo {
      color: v.$blanco;
      background-color: v.$negro;
      border-radius: 4rem;
    }
  }
}

.buscador {
  background-color: v.$gris-claro;
  display: flex;
  align-items: center;
  padding: 0 0 0 1.5rem;
  border-radius: 5rem;
  flex-grow: 1;

  input[type="text"] {
    background-color: v.$gris-claro;
    border-radius: 5rem;
    padding: 1rem;
    border: none;
    &:focus {
      border: none;
      outline: none;
    }
  }

  &__icono {
    font-size: 2rem;
    color: v.$grisOscuro;
  }
}
