@use 'base/variables' as v;
@use 'base/mixins' as m;

.grid-container {
  margin: 0 auto;
}

.grid-item {
  width: 230px;
  margin-bottom: 40px;
  position: relative;

  img {
    display: block;
    border-radius: 15px;
  }

  &:hover .opciones {
    display: flex;
  }
}

.opciones {
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  background-color: #00000052;
  border-radius: 15px;
  padding: 1.5rem;
  cursor: zoom-in;
  top: 0;
  left: 0;

  &__botones {
    text-align: end;
   
  }
}

.btn_guardar {
    background-color: v.$rojo;
    color: v.$blanco;
    border: none;
    padding: 1rem;
    border-radius: 15rem;
    font-size: 1rem;
    font-weight: 600;
}

.icono {
    background-color: v.$gris-claro;
    padding: 0.5rem;
    border-radius: 50%;
    font-size: 1.2rem;

    &:hover{
        background-color: v.$blanco;
        cursor: pointer;
    }
  }
