@use 'variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin resetear-lista{
    margin: 0;
    padding:0;
    list-style: none;
}

@mixin sombra {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

@mixin boton($bgColor, $display ) {
    background-color: $bgColor;
    color: v.$blanco;
    font-weight: v.$bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: v.$separacion / 2;
    display: $display;
    border: none;

    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }
}

@mixin menu-social($color, $size) {
    &__enlace {
      &::before {
        color: $color;
        font-size: $size;
        display: inline-block;
        font-style: normal;
        text-rendering: auto;
        -webkit-font-smoothing: antiliased;
        font-family: "Font Awesome 6 Brands";
      }
  
      &[href*="https://facebook.com"]::before
      {
        content: "\f39e";
      }
      &[href*="https://github.com"]::before
      {
        content: "\f09b";
      }
  
      &[href*="https://tiktok.com"]::before
      {
        content: "\e07b";
      }
      &[href*="https://instagram.com"]::before
      {
        content: "\f16d";
      }
      &[href*="https://youtube.com"]::before
      {
        content: "\f167";
      }
      &[href*="https://twitter.com"]::before
      {
        content: "\f099";
      }
    }
  
    &__ocultar {
      display: none;
    }
  }